.gora{
    width: 100%;
    height: 5rem;
    background-color: #fff;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Josefin Sans', sans-serif;
    z-index: 999;
}

.goraSrodek{
    flex: 6 1;
}

.goraLewo, .goraPrawo{
    flex: 3 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ikonyGora{
    font-size: 20px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
}

.goraObraz{
width: 40px;
height: 40px;
border-radius: 50%;
object-fit:s cover;
}

.menu{
display: flex;
justify-content: center;
margin: 0;
padding: 0;
list-style: none;
}

.pozycjaMenu{
margin-right: 20px;
font-size: 18px;
font-weight: 300;
cursor: pointer;
}

.ikonaSzukania{
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
    
}
.login {
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url(https://images.pexels.com/photos/1229861/pexels-photo-1229861.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
  background-size: cover;
}

.loginTytul {
  font-size: 50px;
}

.loginFormularz {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.loginFormularz > label {
  margin: 10px 0;
}

.loginInput {
  padding: 10px;
  background-color: white;
  border: none;
}

.przyciskLogin {
  margin-top: 20px;
  cursor: pointer;
  background-color: lightcoral;
  border: none;
  color: white;
  border-radius: 10px;
  padding: 10px;
}

.przyciskLoginZarejestruj{
    position: absolute;
    top: 100px;
    right: 20px;
    background-color: teal;
    cursor: pointer;
    border: none;
    padding: 10px;
    color: white;
    border-radius: 10px;
    
}

.napisz {
  padding-top: 50px;
}

.napiszObraz{
    margin-left: 150px;
    width: 70vw;
    height: 250px;
    border-radius: 10px;
    object-fit: cover;
}

.napiszFormularz {
  position: relative;
}

.napiszFormularzGrupa {
  margin-left: 150px;
  display: flex;
  align-items: center;
}

.napiszIkona {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: gray;
  cursor: pointer;
}

.napiszInput {
  font-size: 30px;
  border: none;
  padding: 20px;
  width: 70vw;
}
.napiszInput:focus {
  outline: none;
}

.napiszTekst {
  font-size: 20px;
  height: 100vh;
}

.napiszPotwierdz {
  position: absolute;
  top: 20px;
  right: 50px;
  color: white;
  background-color: teal;
  padding: 10px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
}

.pojedynczy{
    display: flex;
}
.pasekboczny{
    flex: 3 1;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.elementPaska{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.tytulWPasku{
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round', sans-serif;
    font-size: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}

.elementPaska > img{
    width: 250px;
    margin-top: 15px;
}

.elementPaska > p{
    padding: 30px;
}

.listaWPasku{
    list-style: none;
    margin-bottom: 30px;
}

.elementListyWPasku{
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
}

.spolecznoscioweWPasku{
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ikonyWPasku{
    font-style: 16px;
    margin-left: 10px;
    cursor: pointer;
} 




.pojedynczyWpis{
    flex: 9 1;
}

.wrapperPojedynczyWpis{
    padding: 20px;
    padding-right: 0;
}

.obrazPojedynczyWpis{
    width: 100%;
    height: 300px;
    border-radius: 5px;
    object-fit: cover;
}

.tytulPojedynczyWpis{
    text-align: center;
    margin: 10px;
    font-family: 'Lora', serif;
    font-size: 28px;
}

.edycjaPojedynczyWpis{
    float: right;
    font-size: 16px;
}

.ikonaPojedynczyWpis{
    margin-left: 10px;
    cursor: pointer;
}

.ikonaPojedynczyWpis:first-child{
    color: teal;
}
.ikonaPojedynczyWpis:last-child{
    color: tomato;
}

.infoPojedynczyWpis{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    font-family: 'Varela Round', sans-serif;
    color: #b39656;
}

.opisPojedynczyWpis{
    color: #666;
    font-size: 18px;
    line-height: 25px;
}
.opisPojedynczyWpis::first-letter{
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;

}


